.App {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(231, 248, 255);
}

.container {
    width: 400px;
    border: 2px solid black;
    border-radius: 0px;
    padding: 30px;
    background-color: rgb(255, 255, 255);
}

.field {
    border: 0px solid black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
}

.field-header {
    font-size: large;

}

.field-value {
    font-size: larger;
}

.buttons {
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.button {

  font-size: normal;
  font-weight: bold;
  padding: 4px 15px 4px 15px;

}
